.glass-btn{
position: relative;
}
.glass-btn button{
color: black;
padding: 0.5em 2em;
font-size:1.2em ;
cursor:pointer ;
letter-spacing:2px ;
}
.glass-btn button, button::before ,button:after{
background: rgba(255, 255, 255, 0.15);
box-shadow: 0 8px 32px 0 rgba(31,38,135,0.37);
-webkit-backdrop-filter: blur(7.5px);
        backdrop-filter: blur(7.5px);
border-radius: 10px;
border:1px solid rgba(255,255,255,0.18);
transition:0.3s;
}
.glass-btn button::before ,button:after{
content: '';
position: absolute;
-webkit-animation: float 4s infinite;
        animation: float 4s infinite;

}
.glass-btn button::before {
width: -0.7em;
bottom: -0.9em;
width: 1.5em;
height: 1.5em;
left: -0.4em;
-webkit-animation-delay: 0.5s;
        animation-delay: 0.5s;

}
.glass-btn button::after {
right: -0.9em;
top: -0.8em;
width: 2em;
height: 2em;

}
.glass-btn span {
position: absolute;
top: 50%;
left:50%;
-webkit-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
width: 6em;
height: 50%;
border-radius: 50%;
z-index: -1;

}
