@-webkit-keyframes float{
  50%{-webkit-transform:translate(-0.5em);transform:translate(-0.5em);}
  100%{-webkit-transform: translate(0);transform: translate(0);}
}
@keyframes float{
  50%{-webkit-transform:translate(-0.5em);transform:translate(-0.5em);}
  100%{-webkit-transform: translate(0);transform: translate(0);}
}

