  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.image {
  width: 100%;
  transition: -webkit-transform 1.1s ease-in-out;
  transition: transform 1.1s ease-in-out;
  transition: transform 1.1s ease-in-out, -webkit-transform 1.1s ease-in-out;
  transition-delay: 0.4s;
}

.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6;
}

.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}

.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}

.panel .content {
  color: #fff;
  transition: -webkit-transform 0.9s ease-in-out;
  transition: transform 0.9s ease-in-out;
  transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
  transition-delay: 0.6s;
}

.panel h3 {
  font-weight: 600;
  line-height: 1;
  font-size: 1.5rem;
}

.panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}

.btnlogin.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}

.right-panel .image,
.right-panel .content {
  -webkit-transform: translateX(800px);
          transform: translateX(800px);
}

/* ANIMATION */

.containerlogin.sign-up-mode:before {
  -webkit-transform: translate(100%, -50%);
          transform: translate(100%, -50%);
  right: 52%;
}

.containerlogin.sign-up-mode .left-panel .image,
.containerlogin.sign-up-mode .left-panel .content {
  -webkit-transform: translateX(-800px);
          transform: translateX(-800px);
}

.containerlogin.sign-up-mode .signin-signup {
  left: 25%;
}

.containerlogin.sign-up-mode form.sign-up-form {
  opacity: 1;
  z-index: 2;
}

.containerlogin.sign-up-mode form.sign-in-form {
  opacity: 0;
  z-index: 1;
}

.containerlogin.sign-up-mode .right-panel .image,
.containerlogin.sign-up-mode .right-panel .content {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.containerlogin.sign-up-mode .left-panel {
  pointer-events: none;
}

.containerlogin.sign-up-mode .right-panel {
  pointer-events: all;
}

@media (max-width: 870px) {
  .containerlogin {
    min-height: 800px;
    height: 100vh;
  }
  .signin-signup {
    width: 100%;
    top: 95%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .signin-signup,
  .containerlogin.sign-up-mode .signin-signup {
    left: 50%;
  }

  .panels-containerlogin {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .right-panel {
    grid-row: 3 / 4;
  }

  .left-panel {
    grid-row: 1 / 2;
  }

  .image {
    width: 200px;
    transition: -webkit-transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .panel .content {
    padding-right: 15%;
    transition: -webkit-transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .panel h3 {
    font-size: 1.2rem;
  }

  .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .btnlogin.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .containerlogin:before {
    width: 1500px;
    height: 1500px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }

  .containerlogin.sign-up-mode:before {
    -webkit-transform: translate(-50%, 100%);
            transform: translate(-50%, 100%);
    bottom: 32%;
    right: auto;
    right: initial;
  }

  .containerlogin.sign-up-mode .left-panel .image,
  .containerlogin.sign-up-mode .left-panel .content {
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
  }

  .containerlogin.sign-up-mode .right-panel .image,
  .containerlogin.sign-up-mode .right-panel .content {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }

  .right-panel .image,
  .right-panel .content {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }

  .containerlogin.sign-up-mode .signin-signup {
    top: 5%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
}

@media (max-width: 570px) {
  form {
    padding: 0 1.5rem;
  }

  .image {
    display: none;
  }
  .panel .content {
    padding: 0.5rem 1rem;
  }
  .containerlogin {
    padding: 1.5rem;
  }

  .containerlogin:before {
    bottom: 72%;
    left: 50%;
  }

  .containerlogin.sign-up-mode:before {
    bottom: 28%;
    left: 50%;
  }
}

