.appdash {
	background-color: var(--theme-bg-color);
     width: 1508px;
	
	height: 95vh;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	border-radius: 14px;
	backdrop-filter: blur(20px);
	-webkit-backdrop-filter: blur(20px);
	font-size: 15px;
	font-weight: 500;
	/* margin-top: -10vh; */
	position: absolute;
	margin: 1vw 1vw;

}

.header {
	display: flex;
	align-items: center;
	flex-shrink: 0;
	height: 58px;
	width: 100%;
	border-bottom: 1px solid var(--border-color);
	padding: 0 30px;
	white-space: nowrap;
}
@media screen and (max-width: 480px) {
	.header {
		padding: 0 16px;
	}
}
.header-menu {
	display: flex;
	align-items: center;
}
.header-menu a {
	padding: 20px 30px;
	text-decoration: none;
	color: var(--inactive-color);
	border-bottom: 2px solid transparent;
	transition: 0.3s;
}
@media screen and (max-width: 610px) {
	.header-menu a:not(.main-headerdash-link) {
		display: none;
	}
}
.header-menu a.is-active,
.header-menu a:hover {
	color: var(--theme-color);
	border-bottom: 2px solid var(--theme-color);
}

@media screen and (max-width: 1055px) {
	.notify {
		display: none;
	}
}

.menu-circle {
	width: 15px;
	height: 15px;
	background-color: #f96057;
	border-radius: 50%;
	box-shadow: 24px 0 0 0 #f8ce52, 48px 0 0 0 #5fcf65;
	margin-right: 195px;
	flex-shrink: 0;
}
@media screen and (max-width: 945px) {
	.menu-circle {
		display: none;
	}
}




.profile-img {
	width: 32px;
	height: 32px;
	border-radius: 50%;
	-o-object-fit: cover;
	object-fit: cover;
	border: 2px solid var(--theme-color);
	margin-left: 22px;
}

.wide .header-menu,
.wide .header-profile {
	display: none;
}
.wide .search-bar {
	max-width: 600px;
	margin: auto;
	transition: 0.4s;
	box-shadow: 0 0 0 1px var(--border-color);
	padding-left: 0;
}
.wide .menu-circle {
	margin-right: 0;
}

.wrapper {
	display: flex;
	flex-grow: 1;
	overflow: hidden;
}

.left-side {
	flex-basis: 240px;
	border-right: 1px solid var(--border-color);
	padding: 26px;
	overflow: auto;
	flex-shrink: 0;
}
@media screen and (max-width: 945px) {
	.left-side {
		display: none;
	}
}

.side-wrapper + .side-wrapper {
	margin-top: 20px;
}

.side-title {
	color: var(--inactive-color);
	margin-bottom: 14px;
}

.side-menu {
	display: flex;
	flex-direction: column;
	white-space: nowrap;
}
.side-menu a {
	text-decoration: none;
	color: var(--theme-color);
	display: flex;
	align-items: center;
	font-weight: 400;
	padding: 10px;
	font-size: 14px;
	border-radius: 6px;
	transition: 0.3s;
}
.side-menu a:hover {
	background-color: var(--hover-menu-bg);
}
.side-menu svg {
	width: 16px;
	margin-right: 8px;
}

.updates {
	position: relative;
	top: 0;
	right: 0;
	margin-left: auto;
	width: 18px;
	height: 18px;
	font-size: 11px;
}

.main-headerdash {
	display: flex;
	align-items: center;
	border-bottom: 1px solid var(--border-color);
	height: 58px;
	flex-shrink: 0;
}
.main-headerdash .header-menu {
	margin-left: 150px;
}
@media screen and (max-width: 1055px) {
	.main-headerdash .header-menu {
		margin: auto;
	}
}
.main-headerdash .header-menu a {
	padding: 20px 24px;
}

.main-containerdash {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

.menu-link-main {
	text-decoration: none;
	color: var(--theme-color);
	padding: 0 30px;
}
@media screen and (max-width: 1055px) {
	.menu-link-main {
		display: none;
	}
}

.content-wrapper {
	display: flex;
	flex-direction: column;
	color: var(--theme-color);
	padding: 20px 40px;
	height: 100%;
	overflow: auto;
	background-color: var(--theme-bg-color);
}
@media screen and (max-width: 510px) {
	.content-wrapper {
		padding: 20px;
	}
}
.content-wrapper-header {
	display: flex;
	align-items: center;
	width: 79.8vw;
	justify-content: space-between;
	background-image: url("https://www.transparenttextures.com/patterns/cubes.png"),
		linear-gradient(
			to right top,
			#cf4af3,
			#e73bd7,
			#f631bc,
			#fd31a2,
			#ff3a8b,
			#ff4b78,
			#ff5e68,
			#ff705c,
			#ff8c51,
			#ffaa49,
			#ffc848,
			#ffe652
		);
	border-radius: 14px;
	padding: 20px 25px;

}
@media screen and (max-width: 415px) {
	.content-wrapper-header {
		padding: 20px;
	}
}
.content-wrapper.overlay {
	pointer-events: none;
	transition: 0.3s;
	background-color: var(--overlay-bg);
}

.overlay-app {
	width: 100%;
	height: 100%;
	position: fixed;
	left: 0;
	top: 0;
	pointer-events: all;
	background-color: rgba(36, 39, 59, 0.8);
	opacity: 0;
	visibility: hidden;
	transition: 0.3s;
}
.overlay-app.is-active {
	visibility: visible;
	opacity: 1;
}

.img-content {
	font-weight: 500;
	font-size: 17px;
	display: flex;
	align-items: center;
	margin: 0;
}
.img-content svg {
	width: 28px;
	margin-right: 14px;
}

.content-text {
	font-weight: 400;
	font-size: 14px;
	margin-top: 16px;
	line-height: 1.7em;
	color: #ebecec;
	display: -webkit-box;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;
}

.content-wrapper-context {
	max-width: 350px;
}

.content-button {
	background-color: #3a6df0;
	border: none;
	padding: 8px 26px;
	color: #fff;
	border-radius: 20px;
	margin-top: 16px;
	cursor: pointer;
	transition: 0.3s;
	white-space: nowrap;
}

.content-wrapper-img {
	width: 186px;
	-o-object-fit: cover;
	object-fit: cover;
	margin-top: -25px;
	-o-object-position: center;
	object-position: center;
}
@media screen and (max-width: 570px) {
	.content-wrapper-img {
		width: 110px;
	}
}

.content-sectiondash {
	margin-top: 30px;
	display: flex;
	flex-direction: column;
}
.content-sectiondash-title {
	color: var(--content-title-color);
	margin-bottom: 14px;
}
.content-sectiondash ul {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	justify-content: space-around;
	background-color: var(--content-bg);
	padding-left: 0;
	margin: 0;
	border-radius: 14px;
	border: 1px solid var(--theme-bg-color);
	cursor: pointer;
}
.content-sectiondash ul li {
	list-style: none;
	padding: 10px 18px;
	display: flex;
	align-items: center;
	font-size: 16px;
	width: 100%;
	height: 100%;
	white-space: nowrap;
	transition: 0.3s;
}
.content-sectiondash ul li:hover {
	background-color: var(--theme-bg-color);
}
.content-sectiondash ul li:hover:first-child {
	border-radius: 13px 13px 0 0;
}
.content-sectiondash ul li:hover:last-child {
	border-radius: 0 0 13px 13px;
}
.content-sectiondash ul li + li {
	border-top: 1px solid var(--border-color);
}
.content-sectiondash ul svg {
	width: 28px;
	border-radius: 6px;
	margin-right: 16px;
	flex-shrink: 0;
}

.productsdash {
	display: flex;
	align-items: center;
	width: 150px;
}
@media screen and (max-width: 480px) {
	.productsdash {
		width: 120px;
	}
}

.statusdash {
	margin-left: auto;
	width: 140px;
	font-size: 15px;
	position: relative;
}
@media screen and (max-width: 700px) {
	.statusdash {
		display: none;
	}
}
.statusdash-circle {
	width: 6px;
	height: 6px;
	background-color: #396df0;
	position: absolute;
	border-radius: 50%;
	top: 4px;
	left: -20px;
}
.statusdash-circle.green {
	background-color: #3bf083;
}
.statusdash-button {
	font-size: 15px;
	margin-top: 0;
	padding: 6px 24px;
}
@media screen and (max-width: 390px) {
	.statusdash-button {
		padding: 6px 14px;
	}
}
.statusdash-button.open {
	background: none;
	color: var(--button-inactive);
	border: 1px solid var(--button-inactive);
}
.statusdash-button:not(.open):hover {
	color: #fff;
	border-color: #fff;
}

.content-button:not(.open):hover {
	background: #1e59f1;
}

.menu {
	width: 5px;
	height: 5px;
	background-color: var(--button-inactive);
	border-radius: 50%;
	box-shadow: 7px 0 0 0 var(--button-inactive), 14px 0 0 0 var(--button-inactive);
	margin: 0 12px;
}

@media screen and (max-width: 415px) {
	.adobe-product .menu {
		display: none;
	}
}
.dropdown {
	position: relative;
	height: 53px;
	width: 40px;
	top: -24px;
	display: flex;
	left: -5px;
	background: transparent;
	border: none;
	cursor: pointer;
}
.dropdown ul {
	position: absolute;
	background: var(--dropdown-bg);
	height: 110px;
	width: 120px;
	right: 0;
	top: 20px;
	pointer-events: none;
	opacity: 0;
	transform: translatey(10px);
	transition: all 0.4s ease;
}
.dropdown ul li a {
	text-decoration: none;
	color: var(--theme-color);
	font-size: 12px;
}

.dropdown.is-active ul {
	opacity: 1;
	pointer-events: all;
	transform: translatey(25px);
}
.dropdown.is-active ul li:hover {
	background-color: var(--dropdown-hover);
}

.button-wrapper {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	width: 187px;
	margin-left: auto;
}
@media screen and (max-width: 480px) {
	.button-wrapper {
		width: auto;
	}
}

.pop-up {
	position: absolute;
	padding: 30px 40px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	overflow-y: auto;
	box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.4);
	transition: all 0.3s;
	z-index: 10;
	background-color: var(--popup-bg);
	width: 500px;
	visibility: hidden;
	opacity: 0;
	border-radius: 6px;
	display: flex;
	flex-direction: column;
	white-space: normal;
}
@media screen and (max-width: 570px) {
	.pop-up {
		width: 100%;
	}
}
.pop-up.visible {
	visibility: visible;
	opacity: 1;
}
.pop-up__title {
	padding-bottom: 20px;
	border-bottom: 1px solid var(--border-color);
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.pop-up__subtitle {
	white-space: normal;
	margin: 20px 0;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.8em;
}
.pop-up__subtitle a {
	color: var(--theme-color);
}

.content-button-wrapper .content-button.statusdash-button.open.close {
	width: auto;
}

.content-sectiondash .close {
	margin-right: 0;
	width: 24px;
}

.checkbox-wrapper {
	display: flex;
	align-items: center;
	font-size: 14px;
	font-weight: 400;
}
.checkbox-wrapper + .checkbox-wrapper {
	margin: 20px 0 40px;
}

.checkbox {
	display: none;
}

.checkbox + label {
	display: flex;
	align-items: center;
}
.checkbox + label:before {
	content: "";
	margin-right: 10px;
	width: 15px;
	height: 15px;
	border: 1px solid var(--theme-color);
	border-radius: 4px;
	cursor: pointer;
	flex-shrink: 0;
}

.checkbox:checked + label:before {
	background-color: #3a6df0;
	border-color: #3a6df0;
	background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24' fill='none' stroke='%23fff' stroke-width='3' stroke-linecap='round' stroke-linejoin='round' class='feather feather-check'%3e%3cpath d='M20 6L9 17l-5-5'/%3e%3c/svg%3e");
	background-position: 50%;
	background-size: 12px;
	background-repeat: no-repeat;
}

.content-button-wrapper {
	margin-top: auto;
	margin-left: auto;
}
.content-button-wrapper .open {
	margin-right: 8px;
}

.apps-card {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	width: calc(100% + 20px);
}

.app-carddash {
	display: flex;
	flex-direction: column;
	width: calc(33.3% - 20px);
	font-size: 16px;
	background-color: var(--content-bg);
	border-radius: 14px;
	border: 1px solid var(--theme-bg-color);
	padding: 20px;
	cursor: pointer;
	transition: 0.3s ease;
}
.app-carddash:hover {
	transform: scale(1.02);
	background-color: var(--theme-bg-color);
}
.app-carddash svg {
	width: 28px;
	border-radius: 6px;
	margin-right: 12px;
	flex-shrink: 0;
}
.app-carddash + .app-carddash {
	margin-left: 20px;
}
.app-carddash span {
	display: flex;
	align-items: center;
}
.app-card__subtext {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.6em;
	margin-top: 20px;
	border-bottom: 1px solid var(--border-color);
	padding-bottom: 20px;
}
.app-card-buttons {
	display: flex;
	align-items: center;
	margin-left: auto;
	margin-top: 16px;
}
@media screen and (max-width: 1110px) {
	.app-carddash {
		width: calc(50% - 20px);
	}
	.app-carddash:last-child {
		margin-top: 20px;
		margin-left: 0px;
	}
}
@media screen and (max-width: 565px) {
	.app-carddash {
		width: calc(100% - 20px);
		margin-top: 20px;
	}
	.app-carddash + .app-carddash {
		margin-left: 0;
	}
}

