.dashboard {
  width: 100vw;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr 5fr;
  position: absolute;
}

.dashboardContainer {
  /* border-left: 1px solid rgba(0, 0, 0, 0.13); */
  /* background-color: rgb(255, 255, 255); */
  padding: 1.6rem 0;
}

.dashboardContainer > h3 {
  color:black;
  font: 300 1.4rem "Roboto";
  text-align: center;
  padding: 0.5rem 2rem;
  margin: auto;
  background-image: url("https://www.transparenttextures.com/patterns/cubes.png"),
		linear-gradient(
			to right top,
			#cf4af3,
			#e73bd7,
			#f631bc,
			#fd31a2,
			#ff3a8b,
			#ff4b78,
			#ff5e68,
			#ff705c,
			#ff8c51,
			#ffaa49,
			#ffc848,
			#ffe652
		);

}

.dashboardSummary {
  margin: 2rem 0;
}

.dashboardSummary > div {
  display: flex;
  /* background-color: white; */
  justify-content: center;
}
.dashboardSummary > div > p {
  /* background-color: rgba(70, 117, 218, 0.932); */
  color: white;
  font: 300 1.3rem "Roboto";
  text-align: center;
  padding: 1.5rem;
  width: 100%;
  margin: 0 2rem;
}
.dashboardSummaryBox2 > a {
  color: white;
  font: 300 1.3rem "Roboto";
  text-align: center;
  background-color: rgb(70, 70, 70);
  text-decoration: none;
  padding: 1.5rem;
  width: 5vmax;
  height: 5vmax;
  margin: 2rem;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dashboardSummaryBox2 > a:first-child {
	background-color: rgb(70, 70, 70);
  color: rgb(255, 255, 255);
}

.dashboardSummaryBox2 > a:last-child {

  color: rgb(255, 255, 255);
}
.chartsld{
	display: flex;
	justify-content: space-around;
	/* background: white; */
	
}
.lineChart {
	cursor: pointer;
  width: 50%;
  /* margin: auto; */
  color: white;
}

.doughnutChart {
	cursor: pointer;
  width: 20vmax;
  /* margin: auto; */
}

@media screen and (max-width: 600px) {
  .dashboard {
    grid-template-columns: 1fr;
  }

  .dashboardContainer {
    border-left: none;
  }

  .dashboardSummary > div > p {
    margin: 0;
  }

  .dashboardSummaryBox2 > a {
    padding: 0.5rem;
    margin: 1rem;
    font: 300 0.9rem "Roboto";
  }
}
