.paymentContainer {
  display: grid;
  place-items: center;
  /* background-color: rgb(255, 255, 255); */
  height: 65vh;
  margin: 2vmax;
  margin-left: -60vh;
}

.paymentForm {
  width: 22%;
  height: 100%;
}

.paymentForm > p {
  font: 400 2vmax "Roboto";
  color: white;
  border-bottom: 1.5px solid white;
  padding: 1vmax 0;
  text-align: center;
  width: 40vh;
  margin: auto;
}

.paymentForm > div {
  display: flex;
  align-items: center;
  margin: 2vmax 0;
  /* width: 40vw; */
}
.adjust-cardinfo{
  width: 30vw;
  height:80vh;
  margin: auto;
  margin-top:-3vh;
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.4);
  border-radius: 1rem;
  z-index: 2;
  /* position: absolute; */
}

.paymentInput {
  /* padding: 1vmax 1vmax; */
  padding-right: 1vmax;
  width:180vh;
  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  outline: none;
  /* position: absolute; */
  padding: 1rem;
  color: white;
}

.paymentForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: white;
  margin-left: -3vw;
  
}

.paymentFormBtn {
  border:none;
  border-radius: 0.4rem;
  background-color: tomato;
  color: white;
  font: 300 0.9vmax "Roboto";
  width: 20vw;
  height: 5vh;
  /* padding: 0.8vmax; */
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
 
}

.paymentFormBtn:hover {
  background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
  .paymentForm {
    width: 90%;
  }

  .paymentForm > p {
    font: 400 8vw "Roboto";
    padding: 4vw 0;
    width: 60%;
  }

  .paymentForm > div {
    margin: 10vw 0;
  }

  .paymentInput {
    padding: 4vw 10vw;
  }

  .paymentForm > div > svg {
    font-size: 6vw;
  }

  .paymentFormBtn {
    font: 300 4vw "Roboto";
    padding: 4vw;
  }
}
